@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

html {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  line-height: 1;
  font-family: "Roboto", sans-serif;
  color: #202020;
  background-color: #fafafa;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.header {
  border-bottom: solid 1px red;
  background-color: red;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
  height: 44px;
  width: 100%;

  img {
    width: 40px;
  }

  nav {
    height: 100%;
    max-width: 922px;
    display: flex;
    align-items: center;
    margin: auto;
  }
}

.content {
  display: flex;
  max-width: 922px;
  margin: auto;
}

.sidebar {
  user-select: none;
  width: 266px;
  height: 100vh;
  padding-top: 74px;
  border-right: 1px solid #ffffff;
  background-color: #fafafa;

  div {
    margin-bottom: 1px;
    cursor: pointer;
    padding: 15px;
    &.active {
      background-color: white;
    }
    .icon {
      margin-right: 10px;
    }
  }
}

.tasks {
  background-color: white;
  height: 100vh;
  width: calc(922px - 266px);
  padding: 74px 28px 0;

  .add-task-btn {
    cursor: pointer;
  }

  .add-task-text {
    color: #545454;
    font-size: 14px;
    margin-left: 2px;
  }
  .add-task-dialog {
    margin-top: 10px;
    input {
      width: 100%;
      border: 1px solid #ddd;
      background-color: white;
      border-radius: 3px;
      height: 35px;
      padding-left: 10px;
      padding-right: 10px;
    }

    .add-btn {
      margin-top: 15px;
      margin-right: 15px;
      cursor: pointer;
    }

    .cancel-btn {
      margin-top: 15px;
      margin-right: 15px;
      cursor: pointer;
    }
    .DayPickerInput {
      display: inline-block;
      float: right;
    }
  }
}
